"use client"

import { useEffect, useState } from "react"

export default function ResultsSection() {
  const [result, setResult] = useState<any>(null)

  useEffect(() => {
    const loadResult = () => {
      const stored = localStorage.getItem("tender_result")
      if (stored) {
        setResult(JSON.parse(stored))
      }
    }

    // ✅ Load on first mount
    loadResult()

    // ✅ Also listen to manual trigger from Upload
    window.addEventListener("tender_result_updated", loadResult)

    return () => {
      window.removeEventListener("tender_result_updated", loadResult)
    }
  }, [])

  const downloadExcel = async () => {
    const res = await fetch(`${process.env.NEXT_PUBLIC_API_BASE}/download_excel`, {
      method: "POST",
      body: JSON.stringify(result),
      headers: { "Content-Type": "application/json" }
    })

    const blob = await res.blob()
    const url = window.URL.createObjectURL(blob)
    const link = document.createElement("a")
    link.href = url
    link.setAttribute("download", "analyzed_tender.xlsx")
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }

  if (!result) return null

  return (
    <div className="bg-gray-100 dark:bg-gray-800 p-6 rounded-xl shadow text-left max-w-4xl mx-auto mt-12">
      <h3 className="text-xl font-bold mb-4">📈 Tender Analysis Result</h3>
      <p className="text-gray-700 dark:text-gray-200 mb-4">
        Аналіз завершено. Натисніть кнопку нижче, щоб завантажити результат у форматі Excel.
      </p>
      <button
        onClick={downloadExcel}
        className="bg-green-600 hover:bg-green-700 text-white font-medium py-2 px-4 rounded"
      >
        ⬇️ Завантажити Excel
      </button>
    </div>
  )
}
